.home header.main, .page-template-pricing header.main, .page-template-titlewsub header.main {
	position:absolute;
	left:0;
	right:0;
	
}

body:not(.home) {
	 header.main {
		.menu {
			ul {
				li {
					&#my-account, &.login {
						display:none !important;	
					}
					
					&:last-child {
						margin-right:0px !important;
						
						a {
							padding-right:0px;	
						}
					}
					
					&.back {
						margin-right:30px;
						
						a {
							@include font-size(11px);
							text-transform:uppercase;
   						letter-spacing: 0.1em;
						}
					}
				}
			}
		}
	}
}

header.main {
  text-align: center;
  padding: 25px 0;
  display:block;
  z-index:11;
  .menu {
	  display:block;
	  font-family: "Helvetica Neue", Arial, sans-serif;
	  margin: 0;
	  float: right;
	  
	  ul {
		  margin: 0;
		  
			  li {
				  display: inline-block;
				  position: relative;
			  	
			  	.cart-contents {
			  		letter-spacing: 1.6px;
					
					i {
						margin:0 10px;
					}
			  	}

				  &.menu-nav {
					  display:none;
					  @media #{$phone} {
						  display:block;		
					  }
				  }
				  &.login, &#my-account {
				  margin-left: 20px;
				  border:1px solid #fff;
				  padding-left:20px;
				  padding-right:20px;
				  @media #{$phone} {
					  margin-right:20px;	
				  }
				  &:hover {
					  background:#fff;
					  
					  a {
						  color:#333;		
					  }
				  }
			  }
								  
			  &.main-link {					
				  margin-right: 30px;	
				  
				  a {
					  @include border-radius(2px);	
					  @include button(#fff);	
				  }
			  }	
			  
			  &.menu-nav a {
				  padding-right:0px;
			  }
			  
			  a	{
				  opacity:.8;
				  color:#fff;
				  text-transform: uppercase;
				  border: 3px solid transparent;
				  display: block;
				  float: left;
				  @include font-size(11px);
				  font-weight: 700;
				  padding: 8px 13px;
				  letter-spacing: 1px;
				  @include transition(all 333ms cubic-bezier(0.5, 0, 0, 1));	
					  
					  &:hover {
						  opacity:1;
					  }
			  }
			  ul.account {
				  display:none;
				  position:absolute;
				  top:120%;
				  text-align:left;
				  left:50%;
				  @include transform(translateX(-50%));
				  width:130px;
				  padding:10px;
				  @include border-radius(2px);
				  border:2px solid #333;
				  
				  li {
					  display:block;
					  
					  a {
						  text-transform:capitalize;
						  width:100%;
						  color:#333;
						  padding: 0 0 5px;
					  }
					  &:last-child a {
						  padding-bottom:0px;
					  }
				  }
				  &:after, &:before {
					 width: 0;
					 height: 0;
					 content: "";
					 display: block;
					 position: absolute;
					 bottom: 100%;
					 right: 50%;
					 @include transform(translateX(50%));
				  }
  
				  &:after {
					 border-bottom: 8px solid #D93664;
					 border-left: 8px solid transparent;
					 border-right: 8px solid transparent;
					 z-index: 1;
					 margin-top: 1px;
				  }
				  
				  &:before {
					 border-bottom: 10px solid #333;
					 border-left: 10px solid transparent;
					 border-right: 10px solid transparent;
					 margin-top: -3px;
					 margin-right: 0px;
					 z-index: -100;
				  }
				  
			  }

	  }

	  &.hamburger {
				  width: 13px;
				  list-style: none;
				  display:inline-block;
				  padding: 0;
				  top: 0px;
				  position: relative;
				  @include transition(all 233ms cubic-bezier(0.5, 0, 0, 1));

					&.checked li {
							width: 11px;
					}
					&.checked li.first {
						@include transform(rotate(-135deg) translateY(-5px));
					}
					
					&.checked li.second {
						@include opacity(0);
					}		
					
					&.checked li.third {
						@include transform(rotate(135deg) translateY(5px));
					}
					li {
						float: left;
						height: 1px;
						background: #fff;
						margin: 2.5px 0 0;
						padding: 0;
						@include transform(translate3d(0, 0, 0));
						@include transition(all 333ms cubic-bezier(0.5, 0, 0, 1));
					
						&.first {
							width: 9px;
						}
						&.second {
							width: 11px;
						}
						&.third {
							width: 13px;
						}								
					}					
			  }	
	 }		
  }
  .logo {
	 float: left;	
	  a {
		  margin-top:6px;
		  display: block;
		  width: auto;
		  height:40px;
		  position:relative;
		  
		  svg {
			  position:absolute;
			  top:auto;
			  width:30px;
			  height:30px;
			  -webkit-transition: all 333ms cubic-bezier(0.5, 0, 0, 1) 0s;
			     -moz-transition: all 333ms cubic-bezier(0.5, 0, 0, 1) 0s;
			      -ms-transition: all 333ms cubic-bezier(0.5, 0, 0, 1) 0s;
			       -o-transition: all 333ms cubic-bezier(0.5, 0, 0, 1) 0s;
			          transition: all 333ms cubic-bezier(0.5, 0, 0, 1) 0s;

			  &:hover {
			  	filter:	drop-shadow( 0 0 3px #eee );
			  	-webkit-filter: drop-shadow( 0 0 3px #eee );
			  }
		  }
	  }
 	}	
	#mobile-nav i {
		margin-left: 5px
	}	
	#mobile-nav i, #mobile-nav i:before, #mobile-nav i:after {
		cursor: pointer;
		@include border-radius(1px);
		height: 5px;
		width: 35px;
		background: #fff;
		position: absolute;
		display: block;
		content: '';
	}
	.slider-tabs {
		 display: inline-block;
		 left: 10%;
		 @include transform(translateX(10%));
		 position: absolute;	
		 
		 div {
			 text-align: center;
		 }	
		  a {
			 @extend %noselect;
			 @include transition(opacity 0.2s ease 0s); 
			 float: none;
			 display: inline-block;
			 cursor: pointer;	
			 margin: 5px 22px 0px 20px;
			 color: #fff;
			 display: block;
			 float: left;
			 font-size: 12px;
			 font-weight: 700;
			 padding: 6px 0px;
			 opacity:.5;
			 text-transform: uppercase;
			 letter-spacing: 1px;
			 color: #fff;
			 &:after  {
				 @include transition(background-color 0.2s ease 0s, width 0.2s ease 0s);
				 background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
				 display: block;
				 height: 1px;
				 width: 0;
				 margin-top:3px;
				 content:'';
			 }			 
			 &:hover, &.active {
				 opacity:1;
			 }
			 &.active:after, &:hover:after {
				 width: 100%;
				 background: #fff;
			 }
		 }
	 }
}

.hidden-sidebar {
		@include transition( all 500ms cubic-bezier(0.25, .1, .25, 1));
		@include transition-timing-function(cubic-bezier(0.25, .1, .25, 1));
		overflow-y: auto;
		-moz-user-select: none;
		background-color: #0c0c0c;
		height: 100%;
		min-height: 100%;
		padding: 30px 40px;
		position: fixed;
		top: auto;
		width: 260px;
		display: block;
		height: 100%;
		right: 0px;
		min-height: 100%;
		.hidden-sidebar-inner {
			color: #fafafa;
			font-size: .875em;
			margin: 0 0 43px;
			position: relative;
			float:left;
			h2 {
				font-size:14px;
				margin:0 0 10px;	
			}
			> div {
				float:left;
				width:100%;
			}
			ul {
				list-style: none none;
				margin: 0 0 40px;
				float:left;
				
				li {
					float:left;
					width:100%;
					padding-bottom: 2px;
					
					&.current-menu-item a {
						color:#fff;
					}
						
					&.main-link {
						a {
							@include border-radius(1px);
							border:3px solid #DC003F;
							color:#DC003F;
							padding:10px 28px;
							text-align:center;
							text-transform:uppercase;
							letter-spacing:1px;
							float:left;
							&:hover {
								 border-color:#DC003F;
								 background:#DC003F;
								 color:#fff;
							}	
							&.one {
								border-color:#625F5A;
								color:#625F5A;	
								
								&:hover {
									background:#625F5A;
									color:#fff;
								}
							}
							&.two {
								border-color:#564591;
								color:#564591;	
								
								&:hover {
									background:#564591;
									color:#fff;
								}						
							}
							&.three {
								border-color:#47B3F5;
								color:#47B3F5;	
								
								&:hover {
									background:#47B3F5;
									color:#fff;
								}							
							}
						}
					}
					a {
						color: rgba(255,255,255,.6);
						font-weight: 400;
						position: relative;
						line-height: 22px;					
						padding-left: 0;
						@include transition-duration(.1s);
						@include transition-property(color);
						@include transition-timing-function(ease-out);
				
							&:hover {
								color: #fff
							}
					}
				}
			}
			#menu-get-started li {
				margin-bottom:7px;
				a {
					display: block;
					font-size: 14px;
					line-height: 19px;
					font-weight: 600;
					letter-spacing: 1px;
					text-transform: uppercase;
				}
			}	
			&.side-menu {
				right: 0
			}	
		}			
}

