footer {
	 float:left;
    padding: 90px 0 120px;
    text-align: left;
    width: 100%;
	
	#logo {
		svg {
			margin-top:1%;
			height: 30px;
			width: 30px;
			
			polygon {
				fill:#333;	
			}
		}
	}
	
	.nav {
		font-family: Lato;
		
		h2 {
			@include font-size(15px);
			margin: 0 0 30px 0;
			color: #000;
			 font-weight: 700;
			 letter-spacing: 1px;
			 margin-bottom: 0;
			 margin-top: 0;
			 padding-bottom: 25px;
			 position: relative;
			 text-transform: uppercase;
		}
		
		.widget-box {
			float:left;
			width:24%;
			margin-right:1%;
			
			ul {
				list-style:none;
				margin:0px;	
				
				li {
					
					a {
					 @include font-size(14px);
					 margin-bottom: 10px;
					 color: #888;
					 display: block;
					 text-transform: capitalize;
					 @include transition-duration(300ms);
					 @include transition-property(color);
					 @include transition-timing-function(cubic-bezier(0.33, 0, 0.2, 1));
					
						&:hover {
							color:#333;	
						}
					}
					&.current_page_item a {
						color:#333;	
					}
				}
			}
		}
	}
}