*,
*:after,
*:before {
  @include box-sizing(border-box);
}
*:focus {
    outline: none;
}
a {
	@include linx(#333, #333, #000, #333);
	text-decoration:none;
	outline:0
}

h1, h2, h3, h4, h5, h6 {
	color: #303030
}
h1 {
	@include font-size(62px);
	margin: 0 0 .67em
}
h2 {
	@include font-size(32px);
	font-weight: 600;
	letter-spacing: 1px;
	line-height: 1.38462em;
	margin: 0 0 .75em;
	text-transform: uppercase
}
h3 {
	@include font-size(22px);
	font-weight: 600;
	letter-spacing: 1px;
	line-height: 1.30435em;
	margin: 0 0 .83em;
	text-transform: uppercase
}
h4 {
	@include font-size(16px);
	font-weight: 400;
	line-height: 1.375em;
	margin: 0 0 1.2em
}
h5 {
	@include font-size(16px);
	font-weight: 600;
	letter-spacing: 1px;
	line-height: 1.57143em;
	margin: 0 0 1.5em;
	text-transform: uppercase
}
h6 {
	@include font-size(14px);
	font-weight: 600;
	letter-spacing: 1px;
	line-height: 1.53846em;
	margin: 0 0 1.67em;
	text-transform: uppercase
}
h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
	color: #303030
}
body {
	@include font-size(16px);
	font-family: proxima-nova, sans-serif;
	color: #666;
	font-weight: 300
}
img {
	max-width: 100%;
	height: auto
}
address, cite, em {
	font-style: italic
}
.clearfix, .row {
	@extend %clearfix;
}
.hide {
  @extend %visuallyhidden;
}

.hr, hr {
	background-color: #e4e4e4;
	border: 0;
	height: 2px;
	margin: 30px 0
}
p {
	margin-bottom: 20px
}
