		}
		.content {
			float: right;
    		width: 80%;
			position: relative;
			@include transition(transform 0.3s ease 0s);
		
			.inner {
				 color: #221e1f;
				 padding: 60px 74px 220px;
				 position: relative;
				 z-index: 2;			 
				 display: none;
				 height: auto;
				 min-height: 0;
				 opacity: 0;
				 overflow: hidden;
				 @include transition(height 0.3s ease 0s);
					
					&.active {
					 display: block;
					 margin-top: 20px;
					 @include transition(opacity 0.3s ease 0s, height 0.3s ease 0s, margin 0.3s ease 0s);
				
						&.show {
							 display: block !important;
							 margin-top: 0;
							 min-height: 500px;
							 opacity: 1;
						}
					}
					h1 {
						 border-bottom: 2px solid #ffffff;
						 color: inherit;
						 font-family: "Lato",sans-serif;
						 font-size: 1.5vw;
						 font-weight: 300;
						 margin: 0 0 2.35vw;
						 padding: 0 0 2.05vw;
						 text-transform: capitalize;	
					}
					h2 {
					 color: inherit;
					 font-family: "PT Serif",Times,serif;
					 font-size: 1.56vw;
					 font-weight: 400;
					 line-height: 1.9vw;
					 margin: 2.35vw 0;	
					}
				}
			&:before {
					 background: #f0f0f0 none repeat scroll 0 0;
					 bottom: 0;
					 content: " ";
					 display: block;
					 left: 0;
					 min-height: 1209px;
					 position: absolute;
					 top: 0px;
					 width: 100vw;
					 z-index: 1;
				}
		}
		
		 .button.get-started {
		 font-family: Lato, sans-serif;
		 text-transform: uppercase;
		 font-weight:700;
		 @include font-size(14px);
		 padding: 10px 15px;
		 letter-spacing:1px;
		 @include button(#333);
		 @include border-radius(3px);
