#theme-wrapper {
	background:#fff;	
	
	.header-title {
		
		h1 {
			color:#fff;
			@include font-size(45px);	
			margin:40px 0 8px 0;
			font-family: Lato;		
		}
		h3 {
			@include font-size(20px);	
			margin: 0 0 100px 0;
			text-transform: none;
			color: rgba(256, 256, 256, .6);
			font-weight:lighter;			
		}			
	}
	.scrollbar {
		@include transition(all 0.2s ease-in-out 0s);	
		background:#EAE9F1;
		float:left;
		width:100%;
		
		.col {
			margin:0px;
		
			.features {
				display:inline-block;
				list-style:none;
				margin:0px;
				
				li {
					margin-right:17px;
					margin-top: 4px;
					float:left;
					
					a {
						 border-bottom: 3px solid rgba(110, 91, 170, 0);
						 color: rgba(110, 91, 170, 0.5);
						 display: block;
						 padding: 1em 0.9em;
						 text-decoration: none;
						 @include transition(all 0.2s ease-in-out 0s);	
						 @include font-size(17px);
						
							&:hover {
							 color: rgba(110, 91, 170, 1);							
							}
					}
					&.active {
						
						a {
							border-bottom:3px solid rgba(110, 91, 170, 1);
							color: rgba(110, 91, 170, 1);						
						}
					}
				}
			}

		}
			&.affix {
				z-index:2;
				top:0px;
				position:fixed;
				width:100%;	
			}		
			
			.estimated {
			display: inline-block;
			padding: 19px 0 19px 18px;
			position: relative;	
			@include font-size(14px);
			line-height:25px;
			color: rgba(110, 91, 170, 0.5);
			
			#cost {
				margin-left:20px;
				@include font-size(20px);	
				position:relative;
				top:2px;
				font-weight:bold;
				margin-right:20px;
				line-height:10px;
				color:#40336C;
			}
		}
		.button {
			float:right;
			@include border-radius(2px);
			@include button(#55448F);	
			padding: 10px 25px;
			vertical-align: middle;
			position:relative;
			top:10px;
			text-transform: uppercase;
			letter-spacing:1px;
			@include font-size(12px);
			@include transition(all 0.2s ease-in-out 0s);	
			
		}
	}
	.features-section {
		h3 {
			@include font-size(22px);
			letter-spacing:0px;
			font-weight:normal;
			text-transform:none;	
			color:#333;
			font-family:lato;
		}
		p {
			font-family:lato;
			@include font-size(15px);
			color:#888;				
		}
		b {
			color:#666;	
		}	
		ul {
			list-style:none;
			margin:0px;
			
			li {
				margin:0 20px 5px 0;	
				padding-bottom:5px;
				@include font-size(14px);
				
				span {
					float:right;
					font-weight:bold;	
					color:#555;
					
					&.circle {
						@include border-radius(30px);
						width:15px;
						height:15px;
						border:1px solid transparent;
						
						&.blue {
							background:#4280E5;	
						}
						&.green {
							background:#5ADE55;
						}
						&.orange {
							background:#FE8B3A;
						}
						&.red {
							background:#FD5A5B;
						}
					}
				}
				&:last-child {
					border:none;	
				}
			}
		}
	}
	.print-section {
		#form-sizes {
			ul {
				text-align:center;
				margin:0px;
				
				li {
					display:inline-block;
					margin-right:3%;
					text-align:center;
					position:relative;
					
					.tooltip {
						display:none;
						position:absolute;
						top:-50px;
						background:#EAE9F1;
						color:#41346D;
						padding:5px 10px;
						left:50%;
						transform: translateX(-50%);
						@include border-radius(3px);	
						@include transition(all 0.2s ease-in-out 0s);	
						
						&:after {
							top: 100%;
							left: 50%;
							border: solid transparent;
							content: " ";
							height: 0;
							width: 0;
							position: absolute;
							pointer-events: none;
							border-color: rgba(234, 233, 241, 0);
							border-top-color: #EAE9F1;
							border-width: 5px;
							margin-left: -5px;
						}
					}
					input {
						margin-top:4px;	
					}
					&.active, &:hover {
						
						.tooltip {
							display:block;	
						}
					}					
					
					&:first-child {
						width:105px;	
					}					
					&:nth-child(2) {
						width:105px;	
					}					
					&:nth-child(3) {
						width:120px;	
					}					
					&:nth-child(4) {
						width:205px;	
					}					
					&:last-child {
						width:205px;
						margin-right:0px;	
					}
					
					img {
					}
				}
			}
		}
	}
	.print-section i {
		border-bottom:1px dotted #aaa;
		color:#999;	
	}
	
	.print-section, .quantity-section {
		h2 {
			font-family:lato;
			@include font-size(24px);
			letter-spacing:0px;
			font-weight:normal;
			text-transform:none;	
			color:#333;
			margin-bottom:10px;
		}
		p {
			@include font-size(16px);
			font-family: lato;
			color:#888;	
		}
	}
	.quantity-section  {
		p {
			font-family: lato;
		
			i {
				color:#999;
				@include font-size(14px);
				margin-right:10px;	
			}
		}
	}
	.faq {
		width:80%;
		margin:100px auto;
		
		ul {
			margin:0px;
			
			li {
				width:47.5%;
				display:inline-block;
				list-style:none;
				margin-right:4%;
				
				&:nth-child(even) {
					margin-right:0px;	
				}
				
				h4 {
					font-weight:bold;	
					font-family: lato;
				}
				p {
					@include font-size(14px);
					color:#777;
					line-height:24px;	
					font-family: lato;
				}
			}
		}
	}
	#container {
		
		#quantity, #print, #features {
			padding:40px 0 80px;	
		}
		#features {
			input[type="checkbox"], input[type="radio"] {
				float:left;
				margin:2px 10px 0 0;	
			}
		}
	}
}
