.home #theme-wrapper, .home #theme-wrapper.zero {

 @include linear-gradient(45deg, #DC003F, #D83E6A);	

	&.one {
		@include linear-gradient(45deg, #444, #625F5A);
		
		.glow-selection:after, header .menu ul li ul.account:after  {
			border-bottom-color:#5D5B56;
		}
	}
	&.two {
		@include linear-gradient(45deg, #3B2F63, #564591);	
		
		.glow-selection:after, header .menu ul li ul.account:after  {
			border-bottom-color:#564591;
		}
	}
	&.three {
		@include linear-gradient(45deg, #255D82,#47B4F7);	
		
		.glow-selection:after, header .menu ul li ul.account:after {
			border-bottom-color:#41A6E4;
		}
	}
	.header-hero {
		background-size: cover;
		
		h1.large {
			margin-top:100px;
			@include font-size(100px);
			font-family: Lato, sans-serif;
			font-weight:300;
			float:left;
		}
		
		p {
			font-family: Lato, sans-serif;
			color:#303030;
			font-weight:500;
			@include font-size(18px);
			margin-bottom:5px;
		}
		
		.button {
			font-family: Lato, sans-serif;
			text-transform: uppercase;
			font-weight:700;
			@include font-size(14px);
			padding: 20px;
			letter-spacing:1px;
			@include button(#333);
			@include border-radius(3px);
			display:block;
			max-width:250px;
			text-align: center;
			@include transition(all 333ms cubic-bezier(0.5, 0, 0, 1) 0s);
		}
		.elements {
			z-index:-1;
			position:absolute;
			left:0;
			top:0;
			bottom:0;
			right:0;
			height:100%;
			width:100%;
			
			.table {
				position:relative;
				top:250px;
				}
		}
		.menu {

			.products {
				margin:0px;

				li {
					position:relative;
					list-style:none;
					float:left;

					h3 {
						display:none;
					}
					&.has-post-thumbnail img.secondary-image {
					    left: 0;
					    opacity: 0;
					    position: absolute;
					    top: 0;
					}
				}
			}
		}
	}
	footer {
		display:none
	}
}

.overlay {
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 11;
	 @include opacity(.7);
	 
	 &.black {
		     background: #000;
	 }
}

body {
	
	&.page-template-form-php {
		
		header {
			position:absolute;	
			width:100%;
		}
		.greybg {
			background :#333;	
			overflow:hidden;
		}
		
		p {
			line-height: 25px;
			color:#aaa;	
		}
		
		article {
			position:relative;
			height:600px;
		
			h4 {
				color:#888;
				@include font-size(12px);	
				font-family: Lato,sans-serif;
				margin: 0 0 5px;
			}
			h1 {
				color:#fff;
				@include font-size(50px);	
				font-family: Lato,sans-serif;
			}
			
			picture {
				position:absolute;	
				left:-200px;
			}
			
		}
		
		.ninja-forms-form-wrap {
			margin:30px 0 100px;
			
			.ninja-forms-required-items, .ninja-forms-response-msg  {
				display:none;	
			}
			
			.text-wrap {
				width:46%;
				display:inline-block;
				position: relative;
				
				&:nth-child(2n) {
					margin-right:4%;			
				}
				
				label {
					position: absolute;
					top:1px;
					left:1px;
					right:1px;
					text-transform: uppercase;
					letter-spacing:1px;
					font-weight:400;
					@include font-size(14px);
					padding: 15px 25px;
					margin:0px;
					width:99%;
					
					strong {
						display:none;	
					}
				}
				
				&.ninja-forms-error {
					
					input {
						border-color:red;	
					}
					.ninja-forms-field-error {
						display:none;
					}
					label {
						color:red;	
					}
				}
				
				input {
					border:1px solid #fff;
					background: transparent;	
					padding: 18px 25px 10px;
				}
			}
			
			input[type="submit"] {
				text-transform: uppercase;
				@include font-size(14px);
				padding:8px 25px;
				letter-spacing:1px;
				border:1px solid #ddd;
				color:#fff;
				background:transparent;
				color:#777;	
			}
				
		}
		
	}

}
